<template>
  <div class="input-box fcsb-r-n">
    <div class="label-warrper">
    <label :for="labelFor" v-if="label">{{label}}</label>
    </div>
    <div class="input-warrper">
      <input :type="type"  :id="labelFor" :name="labelFor" :placeholder="placeholder" :value="value" @input="handleInput" @focus="focus" @blur="blur">
    </div>
    <div class="clear-warrper" v-show="iconClearIsShow" @click="clearInput">
      <i class="icon-clear"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: "zjInput",
  props: {
    labelFor: {
      type: String,
      default: "",
      required: false
    },
    label: {
      type: String,
      default: ""
    },
    type: {
      type: String,
      default: "text",
      // 这个值必须匹配下列字符串中的一个
      validator: function(value) {
        return ["text", "number"].indexOf(value) !== -1;
      }
    },
    placeholder: {
      type: String,
      default: ""
    },
    value: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      textValue: "",
      iconClearIsShow: false
    };
  },
  methods: {
    handleInput(event) {
      this.textValue = event.target.value;
      var newValue = this.textValue;
      this.$emit("input", newValue);
    },
    clearInput(event) {
      this.textValue = "";
      var newValue = this.textValue;
      this.$emit("input", newValue);
    },
    focus(event) {
      console.log(event.target.value);
      if (event.target.value == "") {
        this.iconClearIsShow = false;
      } else {
        this.iconClearIsShow = true;
      }
    },
    blur() {
      this.iconClearIsShow = false;
    }
  },
  watch: {
    textValue: function(val) {
      console.log(val);
      if (val == "") {
        this.iconClearIsShow = false;
      } else {
        this.iconClearIsShow = true;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@boder-color: #eaeaea;
@bgc: #fff;
@color: #282828;
.input-box {
  width: 100%;
  background-color: @bgc;
  height: 40px;
  position: relative;
  color: #282828;
}
.label-warrper {
  label {
    display: block;
    font-size: 14px;
    line-height: 1;
    padding-right: 16px;
  }
}
.input-warrper {
  flex: 1;
  // padding-left: 6px;
  input {
    display: block;
    width: 100%;
    font-size: 14px;
    line-height: 1.6em;
    background: #fff;
  }
}
.clear-warrper {
  margin-left: 6px;
  .icon-clear {
    display: block;
    width: 12px;
    height: 12px;
    background: url(./img/icon-delete.svg) no-repeat;
    background-size: cover;
  }
}
</style>
