<template>
  <div class="release-step-four">
    小程序发布-第四步
  </div>
</template>

<script>
// import LinkCell from "../../components/linkCell/linkCell";
export default {
  name: "releaseStepFour",
  data() {
    return {};
  },
  // 组件实例创建完成，属性已绑定，DOM未生成，$el属性不存在
  created() {
    console.log("created");
    console.log(this.$el);
  },
  // 模板编译挂载之前 $el属性不存在
  beforeMount() {
    console.log("beforeMount");
    console.log(this.$el);
  },
  // 模板编译挂载之后 $el属性存在
  mounted() {
    console.log("mounted");
    console.log(this.$el);
    this.init();
  },
  // 组件更新之前
  beforeUpdated() {
    console.log("beforeUpdated 组件更新之前");
    console.log(this.$el);
  },
  // 组件更新之后
  updated() {
    console.log("updated 组件更新完成了");
    console.log(this.$el);
  },
  // for keep-alive，组件被激活时调用
  activated() {
    console.log("activated 组件被激活");
    console.log(this.$el);
  },
  // for keep-alive，组件被移除时调用
  deactivated() {
    console.log("deactivated 组件被移除");
    console.log(this.$el);
  },
  // 组件被销毁前调用
  beforeDestory() {
    console.log("beforeDestory 组件被销毁前调用");
    console.log(this.$el);
  },
  // 组件被销毁后调用
  destory() {
    console.log("destory 组件被销毁后调用");
    console.log(this.$el);
  },
  methods: {
    init() {
      console.log("init func");
    }
  },
  components: {}
};
</script>

